.login-container {
  border: 2px solid #1f53c5;
  background-color: #24303c;
  padding: 10px;
  border-radius: 10px;
  margin-top: 20px;
}

.login-container label {
  color: white;
  padding: 10px;
}

.login-container input {
  background-color: #24303c;
  border: 1px solid #1f53c5;
  border-radius: 5px;
  text-align: justify;
  display: inline-block;
  height: 30px;
  color: white;
  font-size: 16px;
}

.logginp {
  color: white;
  margin-top: 10px;
}

.login-container button {
  background: #1f53c5 !important;
  border: 2px solid #1f53c5;
  border-radius: 28px;
  display: inline-block;
  cursor: pointer;
  color: white;
  font-size: 14px !important;
  padding: 10px;
  margin: 10px;
}
