.userloged {
  margin-top: 10%;
  height: 100vh;
  width: 60%;
  margin-left: 35%;
}

.container {
  width: 100%;
  height: 100%;
  max-width: 600px;
}

.text-loged {
  padding: 10px 10% 15% 10%;
  background: #24303c;
  border: 2px solid #1f53c5;
  border-radius: 23px;
  color: white;
}

.text-loged p {
  font-size: 20px;
  margin-top: 4%;
  padding-bottom: 5%;
}

.text-loged h1 {
  text-align: center;
  font-size: 45px;
}

.text-loged h2 {
  margin-top: 5%;
}

.text-loged button {
  background: #1f53c5 !important;
  border: 2px solid #1f53c5;
  border-radius: 28px;
  display: inline-block;
  cursor: pointer;
  color: white;
  font-size: 14px;
  margin-right: 7%;
  float: right;
  width: 30%;
  height: 50px;
}

.comp-pago {
  float: right;
  margin-left: 50%;
}

.inputarchive button {
  height: 100px;
}

.text-loged button:hover {
  color: rgb(6, 186, 222);
  border: 1px solid rgba(24, 128, 149, 1);
  text-shadow: 0px 1px 0px rgba(24, 128, 149, 1);
  transition: 0.2s;
}
.text-loged button:active {
  position: relative;
  top: 1px;
}

@media screen and (max-width: 850px) {
  .text-loged p {
    font-size: 10px;
    margin-top: 4%;
  }

  .text-loged h1 {
    text-align: center;
    font-size: 25px;
  }

  .text-loged h2 {
    margin-top: 5%;
  }

  .text-loged button {
    width: 80px;
    height: 35px;
    font-size: 10px;
    border-radius: 5px;
  }

  .container {
    margin-top: 50%;
  }

  .userloged {
    margin-left: 20%;
  }
}
